
import { defineComponent } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "SupplierCard",
  setup() {
    const router = useRouter();
    const onCardClick = () => {
      router.push({ name: "SupplierDetail", params: { id: "id" } });
    };

    return {
      onCardClick
    };
  }
});

export default Component;
